html {
  background-color: #3f3f3f;
  color: white;
}

ul {
  list-style-type: none;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App, .aptn-cards {
  text-align: center;
}

.back-apts {
  display: flex;
}

.back-btn-apt {
  background-color: transparent;
  border: none;
  color: none;
  font-size: 30px;
}

.back-btn-apt:active {
  transition: 0.1s;
  transform: scale(1.2);
}

.aptn-cards {
  /* margin: 50%; */
  display: flex;
  flex-direction: column;
  align-items: center;

}

.aptn-card-holder {
  margin: 20px;
  padding: 10px;

  display: flex;
  flex-direction: column;
  background-color: #bbe2b7;
  width: 400px;
  height: 180px; 

  justify-self: center;
  text-align: center;

  color: rgb(49, 49, 49);
  border-radius: 20px;

  font-size: 20px;
  
}

.aptn-card-holder-covid {
  background-color: #ffa9c4;
}

.ptn-name {
  font-size: 40px;
  padding: 5px;
}

.dr-name, .age-date-holder, .symptoms {
  color: rgb(68, 68, 68);
}

.age-date-holder {
  display: flex;
  justify-content: space-between;
  margin: 8px;
}

.delete-btn {
  margin-top: 10px;
  padding: 3px;
  font-size: 20px;
  border-radius: 20px;
  border: none;

  background-color: #cd0000;
  color: white;
  
}

.delete-btn:hover {
  background-color: #d75050;
  transition: 0.2s;
}

.delete-btn:active {
  background-color: #9d0000;
  transition: 0.1s;
}