html {
  background-color: #3f3f3f;
  color: white;
  text-align: center;
}

.landing-div {

  height: 80vh;
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;
}

.move-button {

  margin: 30px;
  padding: 10px;
  font-size: 25px;
  border-radius: 5px;
  border: none;

  background-color: #bcbcbc;
  color: black;

  width:40%;
  height: 100px;
  
}

.move-button:hover {
  background-color: #a5a5a5;
  transition: 0.1s;
}

.move-button:active {
  background-color: #dddddd;
  transition: 0.1s;
}
