html {
  background-color: #3f3f3f;
  color: white;
  text-align: center;
}

.input-form {
  display: flex;
  flex-direction: column;
  margin: 10px;

  align-items: center;

  font-size: 30px;
  margin-top: 100px;
}

.input-form > label {
  margin: 10px;
}

label {
  display: flex;
  justify-content: space-between;
  width: 50%;
}

label > input {
  margin-left: 10px;
  width: 400px;
}

.submit-btn {

  margin-top: 10px;
  padding: 10px;
  font-size: 25px;
  border-radius: 5px;
  border: none;

  background-color: #bcbcbc;
  color: black;

  width:50%;
  
}

.submit-btn:hover {
  background-color: #a5a5a5;
  transition: 0.1s;
}

.submit-btn:active {
  background-color: #dddddd;
  transition: 0.1s;
}

.check {
  transform: scale(2.5);
}